import posed from "react-pose";

const Square = posed.div({
  idle: {
    scale: 1,
  },
  hovered: {
    scale: 1.05,
  }
});

const styles = {
  p: {
    fontFamily: "Roboto",
    color: "black",
  }
}

class Card extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      h1: false,
      h2: false,
    }
  }

  render() {
    return(
      <div className="is-flex columns" style={{padding: "0.25rem"}}>
        <div className="column is-2 is-2-mobile is-flex" style={{alignItems: "center"}}>
          <img src={this.props.src}/>
        </div>
        <div className="column is-10">
          <div className="is-flex" style={{alignItems: "center", padding: 0}}>
            <b className="is-size-5 is-size-6-mobile" style={ styles.p }>{this.props.title} </b>

            </div>
          { this.props.link ? <a className="is-size-6" href={this.props.link}>{this.props.link}</a> : <div></div> }
          { this.props.role ? <p className="is-size-6 is-size-7-mobile" style={{fontFamily: "Roboto", color: "gray"}}>{this.props.role}</p> : <div></div> }
          <div className="is-flex" style={{alignItems: "center", padding: 0}}>
            <p className="is-size-6" style={ styles.p }>{this.props.desc}
            { this.props.github ? (
              <Square
              pose={this.state.h2 ? "hovered" : "idle"}
              onMouseEnter={() => this.setState({ h2: true })}
              onMouseLeave={() => this.setState({ h2: false })}>
                <a href={this.props.github} className="is-flex" style={{alignItems:"flex-end"}}>
                  <span className="icon has-text-black is-size-7" style={{padding: '0px 5px', margin: '0px 5px'}}>
                      <i className="fab fa-lg fa-github is-size-6"></i>
                  </span>
                </a>
              </Square>
              ) : <div></div> }
              </p>
          </div>
        </div>
      </div>
    );
  }
}


//<i className="fas fa-angle-down has-text-grey" style={{marginLeft: 10}}></i>
  export default Card;
